.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 48px;
    width: 100%;
    height: 100vh;
    background: $cl-dk-background;
    transition: 2s ease-out;
    opacity: 1;
    cursor: auto;

    .br-logo {
        width: 224px;
        height: 224px;
        cursor: pointer;
        transition: 0.3s ease;
        &:hover {
            opacity: 0.7;
        }
    }

    .br-title {
        color: $cl-white;
        opacity: 0.7;
        font-weight: lighter;
        font-size: 18px;
        letter-spacing: 12px;
        padding-left: 14px;
    }
}

.roll-off-intro {
    z-index: -10;
    opacity: 0;
}