.area-info {
    display: none;
    position: absolute;
    top: 0;
    left: 0;

    .info {
        position: fixed;
        width: 50%;
        height: 100%;
        padding: 36px 80px;
        right: -50%;
        z-index: -10;
        transition: 1s ease-in-out;
        opacity: 0;
        background: linear-gradient(270deg, #2D2C2B 0%, #2D2C2B 94.32%, rgba(45, 44, 43, 0) 100%);
        overflow: scroll;
        cursor: auto;

        -ms-overflow-style: none;
         &::-webkit-scrollbar{
           display:none;
         }

        .close {
            position: absolute;
            top: 36px;
            right: 32px;
            width: 28px;
            height: 28px;
            border-radius: 50%;
            cursor: pointer;
            transition: 0.7s ease;

            &:hover {
                .bar {
                    background: $cl-white;
                }
            }

            .bar {
                display: block;
                position: absolute;
                top: 14px;
                left: 5px;
                width: 17px;
                height: 1px;
                background: $cl-white;
                transition: 0.3s ease;

                &:first-child {
                    transform : rotate(45deg)
                }

                &:last-child {
                    transform: rotate(135deg);
                }
            }
        }

        .expander {
            position: absolute;
            top: 76px;
            right: 32px;
            width: 28px;
            height: 28px;
            cursor: pointer;
            border-radius: 50%;
            transition: 0.3s ease;

            &:hover {
                .bar {
                    &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                    &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                    &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                    &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                    &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                    &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                    &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                    &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                    &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                }
            }

            .bar {
                position: absolute;
                display: block;
                width: 1px;
                height: 1px;
                background: $cl-white;
                transition: 0.3s ease;
                border-radius: 1px;

                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
            }
        }

        .reduction {
            .bar {
                &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                &:nth-child(2) {top: 6px; left: 6px; width: 5px;}
                &:nth-child(3) {top: 6px; left: 6px; height: 5px;}
                &:nth-child(4) {top: 6px; right: 6px; width: 5px;}
                &:nth-child(5) {top: 6px; right: 6px; height: 5px;}
                &:nth-child(6) {bottom: 6px; left: 6px; width: 5px;}
                &:nth-child(7) {bottom: 6px; left: 6px; height: 5px;}
                &:nth-child(8) {bottom: 6px; right: 6px; width: 5px;}
                &:nth-child(9) {bottom: 6px; right: 6px; height: 5px;}
                
                &:hover {
                    .bar {
                        &:nth-child(1) {top: 11px; left: 11px; width: 6px; height: 6px; background: none; border: 1px solid $cl-white;}
                        &:nth-child(2) {top: 7px; left: 7px; width: 5px;}
                        &:nth-child(3) {top: 7px; left: 7px; height: 5px;}
                        &:nth-child(4) {top: 7px; right: 7px; width: 5px;}
                        &:nth-child(5) {top: 7px; right: 7px; height: 5px;}
                        &:nth-child(6) {bottom: 7px; left: 7px; width: 5px;}
                        &:nth-child(7) {bottom: 7px; left: 7px; height: 5px;}
                        &:nth-child(8) {bottom: 7px; right: 7px; width: 5px;}
                        &:nth-child(9) {bottom: 7px; right: 7px; height: 5px;}
                    }
                }
            }
        }

        .info-content {
            display: flex;
            flex-direction: column;
            gap: 24px;
            .info-sub-title {
                color: $cl-dk-heading;
            }

            .info-header-section {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .info-spec {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;

                    .info-title {
                        padding-right: 16px;
                        color: $cl-dk-heading;
                    }
    
                    .info-spec-wrap {
                        display: flex;
                        gap: 16px;
                        .info-spec-block {
                            display: flex;
                            flex-direction: column;
                            gap: 8px;
                            text-align: left;
                            .copy-body {
                                color: $cl-dk-body;
                                opacity: 0.7;
                                .copy-body-small{
                                    display: none;
                                    padding-left: 6px;
                                    color: $cl-dk-body;
                                    }
                            }
    
                            .copy-body-large {
                                padding: 0;
                                font-size: 22px;
                                color: $cl-dk-heading;
                            }
                        }
                    }
                }

                .info-illust {
                    height: 160px;
                }
            }

            .info-body-section {
                width: 100%;
                .info-sub-title {
                    color: $cl-dk-heading;
                }
                
                .hide-info-sub-title {
                    display: none;
                }

                .info-fac-serve {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                    width: 100%;
                    
                    .info-fac-serve-container {
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        .info-fac-serve-content-wrap {
                            display: flex;
                            flex-direction: column;
                            gap: 12px;
                            .info-fac-serve-list {
                                display: flex;
                                flex-wrap: wrap;
                                gap: 6px;
            
                                li {
                                    padding: 6px 12px;
                                    color: #D5D0C7;
                                    background: #676767; 
                                    font-size: 14px;
                                    line-height: 18px;
                                    cursor: pointer;
                                    border-radius: 4px;
                                }
            
                                .selected {
                                    color: #676767;
                                    background: #D5D0C7;
                                }
                            }
    
                            .info-fac-serve-desc {
                                .info-desc,
                                .info-career {
                                    padding: 12px 0;
                                    white-space: pre-wrap;
                                    .career {
                                        width: max-content;
                                        text-decoration: underline;
                                        margin-right: 4px;
                                        color: $cl-body-text;
                                        opacity: 0.7;
                                        transition: 0.3s ease;
        
                                        &:hover {
                                            opacity: 1;
                                        }
                                    }
                                    .copy-body {
                                        color: $cl-dk-body;
                                    }
                                    .copy-body-small {
                                        color: $cl-dk-body;
                                    }
                                }
                                
                                .hide-desc,
                                .hide-career {
                                    display: none;
                                }
                            }
                        }
    
                        .info-fac-serve-wrap {
                            width: 100%;
                            height: auto;
                            .info-fac-serve-content {
                                display: block;
                                width: 100%;
                                height: 100%;
        
                                .image-wrap-slide {
                                    position: relative;
                                    height: auto;
                                    overflow: hidden;
                                    border-radius: 6px;
    
                                    &:hover {
                                        .button {
                                            opacity: 1;
                                        }
                                    }

                                    .button {
                                        position: absolute;
                                        width: 28px;
                                        height: 28px;
                                        right: 24px;
                                        z-index: 15;
                                        background: rgba(255,255,255,0.7);
                                        border-radius: 50%;
                                        cursor: pointer;
                                        opacity: 0;
                                        transition: 0.3s ease;
    
                                        &:hover {
                                            background: $cl-sub-background;
                                        }
    
                                        img {
                                            position: absolute;
                                            left: 7px;
                                            width: 14px;
                                            height: 14px;
                                        }
                                    }
    
                                    .button-pre {
                                        top: 24px;
                                        img {
                                            top: 7px;
                                        }
                                    }
    
                                    .button-next {
                                        bottom: 24px;
                                        img {
                                            top : 8px;
                                        }
                                    }
    
                                    .hide-button {
                                        display: none;
                                    }
    
                                    .dot-wrap {
                                        display: flex;
                                        height: 100%;
                                        flex-direction: column;
                                        justify-content: center;
                                        gap: 6px;
                                        position: absolute;
                                        right: 36px;
                                        z-index: 10;
                                        .dot {
                                            width: 4px;
                                            height: 18px;
                                            border-radius: 2px;
                                            background: rgba(255,255,255,0.4);
                                            cursor: pointer;
                                            transition: 0.5s ease;
                                            &:hover {
                                                background: $cl-sub-background;
                                            }
                                        }
    
                                        .expand-dot {
                                            height: 48px;
                                            background: #fff;
                                        }
                                    }
    
                                    .hide-dot-wrap {
                                        display: none;
                                    }
    
                                    .image-wrap {
                                        display: flex;
                                        flex-direction: column;
                                        width: 100%;
                                        height: 50vh;
                                        transition: 0.8s ease;
                                        background: #191919;
                                        .image {
                                            position: absolute;
                                            top: 0;
                                            left: 0;
                                            width: 100%;
                                            height: 100%;
                                            opacity: 0;
                                            transition: 0.3s ease;
                                            transform-origin: center center;
                                            margin-bottom: 12px;
                                            img {
                                                width: 100%;
                                                height: 100%;
                                                object-fit: cover;
                                                transition: 0.3s ease;
                                            }
    
                                            &:last-child {
                                                margin-bottom: 0;
                                            }
                                        }
    
                                        .show-image {
                                            opacity: 1;
                                        }
                                    }
                                }
        
                                .info-fac-serve-content-wrap {
                                    padding-top: 24px;
                                    .copy-body-small {
                                        position: relative;
                                        margin-left: 10px;
                                        
                                        i {
                                            display: block;
                                            position: absolute;
                                            top: 4px;
                                            left: -10px;
                                            width: 4px;
                                            height: 4px;
                                            background: $cl-body-text;
                                            border-radius: 2px;
                                        }
                                    }
                                }
            
                                .image-front {
                                    opacity: 1;
                                }
            
                                .image-back {
                                    opacity: 0;
                                }
                            }
            
                            .hide-content {
                                display: none;
                            }
                        }
                    }
                }
                .info-user {
                    width: 100%;
                    .user-card {
                        display: flex;
                        flex-direction: column;
                        justify-content: flex-end;
                        align-items: center;
                        gap: 8px;
                        padding: 12px 6px;
                        border-radius: 6px;
                        text-align: center;
                        .user-image {
                            height: 80px;
                        }
                        .copy-body-large,
                        .copy-body-small {
                            width: 100%;
                            color: $cl-dk-body;
                        }
                    }
    
                    .focused-user {
                        padding: 24px 0;
                        .focused-user-card-list {
                            display: flex;
                            flex-wrap: wrap;
                            gap: 12px;
                            .focused-user-card {
                                p {
                                    color: $cl-dk-body;
                                }
                            }
                        }
                        .focused-user-insight {
                            display: none;
                            .insight {
                                padding: 8px 0;
                                .copy-body-small {
                                    position: relative;
                                    margin-left : 10px;
                                    color: $cl-dk-body;
    
                                    i {
                                        display: block;
                                        position: absolute;
                                        top: 4px;
                                        left: -10px;
                                        width: 4px;
                                        height: 4px;
                                        background: $cl-dk-body;
                                        border-radius: 2px;
                                    }
                                }
                            }
                        }
                    }
    
                    .related-user {
                        padding: 24px 0;
                        .related-user-card-list {
                            display: flex;
                            flex-wrap: wrap;
                            gap: 12px;
                            .related-user-card {
                                p {
                                    color: $cl-dk-body;
                                }
                            }
                        }
                    }
                }
                .info-area-context {
                    display: flex;
                    flex-direction: column;
                    gap: 24px;
                    width: 100%;
                    padding: 24px 0;
                    .area-context-content {
                        display: flex;
                        flex-direction: column;
                        gap: 16px;
                        .area-context-content-wrap {
                            display: flex;
                            gap: 18px;
                            .context-content {
                                display: flex;
                                flex-direction: column;
                                gap: 8px;
                                text-align: left;
                                max-width: 30%;
                                .copy-body-large {
                                    color: $cl-dk-body;
                                    .copy-body-small {
                                        padding-left: 6px;
                                        color: $cl-dk-body;
                                        display: none;
                                    }
                                }
                                
                                .context-content-list {
                                    display: flex;
                                    flex-direction: column;
                                    gap: 8px;
                                    margin-top: 4px;
                                    color: $cl-dk-body;
                                    opacity: 0.7;
    
                                    .copy-body-small {
                                        position: relative;
                                        margin-left: 10px;
        
                                        i {
                                            display: block;
                                            position: absolute;
                                            top: 4px;
                                            left: -10px;
                                            width: 4px;
                                            height: 4px;
                                            background: $cl-body-text;
                                            border-radius: 2px;
                                        }
                                    }   
                                }
                            }
                        }
                        .context-image-wrap {
                        }
                    }
                }
            }
        }
    }

    .info-showing {
        right: 0;
        opacity: 1;
        z-index: 900;
        transition: 1s ease-in-out;
    }

    .info-expanding {
        top: 0px;
        right: 0px;
        width: 100%;
        max-width: 100%;
        height: 100%;
        border-radius: 0;
        // width: calc(100% - 96px);
        // height: calc(100% - 48px);
        background: linear-gradient(270deg, #2D2C2B 100%, #2D2C2B 100%, rgba(45, 44, 43, 0) 100%);
        transition: 1s ease;

        .info-content {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-between;
            
            .info-header-section {
                .info-spec {
                    padding: 0;
                    gap: 12px;
                    .info-spec-wrap {
                        gap: 24px;
                        .info-spec-block {
                            opacity: 0.6;
                            gap: 4px;
                            .copy-body {
                                font-size: 12px;
                            }
                            .copy-body-large {
                                font-size: 16px;
                            }
                        }
                    }
                }
                .info-illust {
                }
            }

            .info-body-section {
                height: 100%;
                .info-fac-serve {
                    width: 100%;
                    border-radius: 8px;
                    height: 100%;
                    .info-fac-serve-container {
                        flex-direction: row;
                        gap: 24px;
                        height: 100%;
                        .info-fac-serve-content-wrap {
                            width: 20%;
                            .info-fac-serve-list {
                                li {
                                    background: #676767;
                                }
                            }
                            .info-fac-serve-desc {
                                .info-desc {
                                    max-height: 600px;
                                    overflow-y: scroll;
                                }
                            }
                        }

                        .info-fac-serve-wrap {
                            width: 80%;
                            height: auto;
                            .info-fac-serve-content {
                                height: 100%;
                                .image-wrap-slide {
                                    height: 100%;
                                    .image-wrap {
                                        height: 100%;
                                        .image {
                                            width: 100%;
                                            height: 100%;
                                            img {
                                                width: 100%;
                                                height: 100%;
                                                object-fit: contain;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                .info-area-context {
                    height: 100%;
                    .area-context-content {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        height: 100%;
                        .area-context-content-wrap {
                            .context-content {
                            }
                        }
                        .context-image-wrap {
                            width: 30%;
                            border: 1px solid red;
                        }
                    }
                }

                .info-user {
                    display: none;
                    flex-direction: column;
                    justify-content: space-between;
                    width: 22%;
                    gap: 16px;
                    .focused-user {
                        height: 50%;
                        padding: 22px 16px;
                        background: $cl-sub-background;
                        border-radius: 8px;
    
                        .focused-user-card-list {
                            .focused-user-card {
                                p {
                                    color: $cl-dk-body;
                                }
                            }
                        }
    
                        .focused-user-insight {
                            width: 100%;
                            margin-top: 24px;
                            display: flex;
                            flex-wrap: wrap;
                            .insight {
                                width: 50%;
                                padding: 24px 12px;
                            }
                        }
                    }
                    .related-user {
                        height: 50%;
                        padding: 22px 16px;
                        border-radius: 8px;
                    }
                }
            }
        }
    }
}

.show-area-info {
    display: block;
}