.s-fin-pin-wrap{
    opacity: 0;
    position: absolute;
    top: calc(50% - $map-half-height);
    left: calc(50% + 170px);
    width: 600px;
    height: $map-height;
    z-index: 10;
    transition: 1s ease;

    .fin-zoning {
        position: absolute;
        width: 600px;
        height: $map-height;
        top: 0;

        img {
            position: absolute;
            top: 0;
            left: -50px;
            width: 650px;
            height: $map-height;
        }
    }

    .masterplan-cabin {
        position: absolute;
        top: calc(50% - $map-half-height);
        left: calc(50% - 300px);
        width: 600px;
        height: $map-height;
        transition: 0.3s ease;
        opacity: 0;
        .masterplan-cabin-img {
            position: absolute;
            top: 0;
            opacity: 0.2;
            transition: 0.3s ease;
        }
        .masterplan-cabin-ab-img {
            top: 133px;
            left: 265px;
            width: 215px;
            height: 452px;
        }
        .masterplan-cabin-cd-img {
            top: 167px;
            left: 47px;
            width: 359px;
            height: 597px;
        }
        .masterplan-cabin-ef-img {
            top: 23px;
            left: 307px;
            width: 256px;
            height: 684px;
        }
        .focus-masterplan-cabin-img {
            opacity: 1;
        }
        .all-masterplan-cabin-img {
            opacity: 1;
        }
    }

    .show-masterplan-cabin {
        opacity: 1;
    }

    .s-fin-pin {
        position: absolute;
        &:hover {
            .area-mark {
                opacity: 1;
                transition: 0.3s ease;
            }
        }
        .area-mark {
            opacity: 0;
            transition: 0.3s ease;
            cursor: pointer;
            &:hover ~ .pin {
                background: orange;
            }
        }

        .pin {
            position: absolute;
            z-index: 50;            
            display: flex;
            align-items: center;
            transition: 0.5s ease;
            cursor: pointer;

            &:hover {
                .pin-title,
                .pin-icon,
                .pin-line {
                    opacity: 1;
                }
            }
            
            .pin-head {
                width: 8px;
                height: 8px;
                top: 1px;
                display: flex;
                justify-content: center;
                border-radius: 50%;
                transition: 0.3s ease;
                font-size: 10px;
                font-family: Helvetica-Bold;
                line-height: 22px;
                background: orange;
                color: $cl-black;
                transition: 0.3s ease;
            }

            .pin-title {
                opacity: 0;
                bottom : 2px;
                width: max-content;
                padding: 0 6px;
                font-size: 10px;
                line-height: 14px;
                color: $cl-white;
                transition: 0.3s ease;
            }
            
            .pin-icon {
                opacity: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border: 0.4px solid $cl-white;
                border-radius: 50%;
                background: $cl-dk-background;
                transition: 0.3s ease;

                img {
                    width: 24px;
                    height: 24px;
                }
            }

            .pin-line {
                opacity: 0;
                height: 0.2px;
                background: $cl-white;
                transition: 0.3s ease;
            }

            .show-pin-head {
                background: $cl-white;
            }

            .show-pin-head,
            .show-pin-line,
            .show-pin-icon,
            .show-pin-title {
                transition: 0.3s ease;
                opacity: 0.6;
            }
        }

        .hide-pin {
            opacity: 0;

            &:hover {
                opacity: 0;
            }
        }
        
        // premium area
        &:nth-child(1) { 
            top: 37px;
            left: 365px;
            width: 49px;
            height: 92px;
            .pin {
                top: 6px;
                left: 21px;
                .pin-line {
                    width: 254px;
                }
            }
        }

        // figga
        &:nth-child(2) { 
            top: 130px;
            left: 365px;
            width: 74px;
            height: 105px;
            .pin {
                top: 24px;
                left: 48px;
                .pin-line {
                    width: 227px;
                }
            }
        }
        // Forest cafe premium
        &:nth-child(3) { 
            top: 219px;
            left: 281px;
            width: 33px;
            height: 29px;
            z-index: 1;
            .pin {
                top: -2px;
                right: 14px;
                flex-direction: row-reverse;
                .pin-line {
                    width: 362px;
                }
            }
        }

        // cocktail & Fire place
        &:nth-child(4) {
            top: 215px;
            left: 372px; 
            width: 73.5px;
            height: 165px;
            .pin {
                top: 38px;
                left: 50px;
                .pin-line {
                    width: 218px;
                }
            }
        }

        // forest garden
        &:nth-child(5) { 
            top: 208px; 
            left: 192px; 
            width: 151px;
            height: 316px;
            .pin {
                top: 110px;
                left: 120px;
                .pin-line {
                    width: 327px;
                }
            }
        }

        // village & forest cafe
        &:nth-child(6) { 
            top: 274px;
            left: 205PX;
            width: 58px;
            height: 113px;
            .pin {
                flex-direction: row-reverse;
                top: 0px;
                right: 42px;
                .pin-line {
                    width: 282px;
                }
            }
        }

        // main centre
        &:nth-child(7) { 
            top: 387px; 
            left: 224px;
            width: 31px;
            height: 44px;
            .pin {
                flex-direction: row-reverse;
                top: 14px;
                right: 18px;
                .pin-line {
                    width: 296px;
                }
            }
        }

        // play garden
        &:nth-child(8) { 
            top: 349px; 
            left: 115px; 
            width: 127px;
            height: 153px;
            .pin {
                flex-direction: row-reverse;
                top: 100px;
                right: 34px;
                .pin-line {
                    width: 266px;
                }
            }
        }

        // forest heart & forest bridge
        &:nth-child(9) { 
            top: 353px; 
            left: 430px;
            width: 110px;
            height: 101px;
            .pin {
                top: 18px;
                left: 36px;
                .pin-line {
                    width: 172px;
                }
            }
        }
        
        // pet area
        &:nth-child(10) { 
            top: 534px;
            left: 230px;
            width: 125px;
            height: 219px;
            .pin {
                top: 120px;
                left: 42px;
                .pin-line {
                    width: 366px;
                }
            }
        }

        /* furnicular
        &:nth-child(11) { 
            top: 570px; 
            left: 183px; 
            width: 27px;
            .pin {
                flex-direction: row-reverse;
                top: -4px;
                right: 12px;
                .pin-line {
                    width: 250px;
                }
            }
        } */
    }

    .s-fin-pin-selected {
        .pin {
                .pin-head {
                    background: orange;
                    color: #191919;
                }
        }

        .area-mark {
            opacity: 1;
        }
    }
}

